.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-70%, 0) rotateZ(20deg);
		@include min-md {
			width: 160px;
			transform: translate(-60%, 0) rotateZ(20deg);
		}
		@include min-lg {
			width: 180px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-2 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 140px;
			transform: translate(20%, 0);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 0% 100%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(60%, 0) scaleX(-1);
		@include min-md {
			width: 160px;
			transform: translate(50%, 0) scaleX(-1);
		}
		@include min-lg {
			width: 180px;
		}
		img {
			transform-origin: 0% 100%;
		}
		.orn-4 {
			position: absolute;
			top: 100%;
			right: 0;
			width: 140px;
			transform: translate(-20%, -30%) rotateZ(20deg);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 50% 100%;
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 5rem 2rem;
		background: var(--color-primary);
		border-radius: 500px;
		overflow: hidden;
		@include max-md {
			padding: 3rem 1rem;
		}
	}
}
